import { axiosInstance } from "../../../../axios-Instance";
import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { queryKeys } from "../../../../react-query/constants";
import { getLoginToken } from "../../../../storage";

// const SERVER_ERROR = "There was an error contacting the server.";

const getAdmin = async () => {
  const data = await axiosInstance({
    url: "/user",
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${getLoginToken()}`,
    },
  });
  return data?.data?.data;
};

export function useAdmin() {
  const fallback = [];
  const { data = fallback } = useQuery({
    queryKey: [queryKeys.admin],
    queryFn: () => getAdmin(),
  });
  return data;
}

async function userReg(formData) {
  const data = await axiosInstance({
    url: "/auth/register",
    method: "POST",
    data: formData,
    headers: {
      "Content-Type": "application/json",
    },
  });

  return data?.data;
}

async function userUpdate(formData) {
  const data = await axiosInstance({
    url: `/user/${formData["_id"]}`,
    method: "PUT",
    data: formData,
    headers: {
      "Content-Type": "application/json",
    },
  });

  return data?.data;
}
async function userDelete(formData) {
  const data = await axiosInstance({
    url: `/user/${formData["_id"]}`,
    method: "DELETE",
    headers: {
      "Content-Type": "application/json",
    },
  });

  return data?.data;
}

export function useRegister() {
  const queryClient = useQueryClient();
  const { mutate, isError, error, isSuccess, reset } = useMutation({
    mutationFn: (formData) => userReg(formData),
    onSuccess: () => {
      queryClient.invalidateQueries([queryKeys.admin]);
    },
  });
  return { mutate, isError, error, isSuccess, reset };
}

export function useUpdateAdmin() {
  const queryClient = useQueryClient();
  const { mutate, isError, error, isSuccess, reset } = useMutation({
    mutationFn: (formData) => userUpdate(formData),
    onSuccess: () => {
      queryClient.invalidateQueries([queryKeys.admin]);
    },
  });
  return { mutate, isError, error, isSuccess, reset };
}

export function useDeleteAdmin() {
  const queryClient = useQueryClient();
  const { mutate, isError, error, isSuccess, reset } = useMutation({
    mutationFn: (formData) => userDelete(formData),
    onSuccess: () => {
      queryClient.invalidateQueries([queryKeys.admin]);
    },
  });
  return { mutate, isError, error, isSuccess, reset };
}
