import React, { useState } from "react";
import Layout from "../../../layout";
import { Table } from "../../../components";
import { useAdmin, useDeleteAdmin, useRegister, useUpdateAdmin } from "./hooks";
import { Modal, Button, FormGroup, Input, PasswordInput } from "mtforms";
import { useIsMutating } from "@tanstack/react-query";
import { errorAlert, successAlert, toastOptions } from "../../../utils";
import { toast } from "react-toastify";

const Admin = () => {
  const isLoading = useIsMutating();
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const [open, setOpen] = useState(false);
  const [edit, setEdit] = useState(false);

  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };
  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  const { mutate, isSuccess, isError, error, reset } = useRegister();
  if (isError) {
    reset();
    errorAlert(error);
  }
  if (isSuccess) {
    reset();
    successAlert("Admin created successfully");
    setFormData("");
    setOpen(false);
  }
  const submitHandler = () => {
    if (formData["password"] !== formData["cpassword"])
      return toast.error("Password doess not match", toastOptions);
    mutate(formData);
  };
  const {
    mutate: update,
    isSuccess: isUpdateSuccess,
    isError: isUpdateError,
    error: errorUpdate,
    reset: updateReset,
  } = useUpdateAdmin();
  if (isUpdateError) {
    updateReset();
    errorAlert(errorUpdate);
    setOpen(false);
  }
  if (isUpdateSuccess) {
    updateReset();
    successAlert("Admin updated successfully");
    setFormData("");
  }
  const updateHandler = () => {
    update(formData);
  };
  const {
    mutate: remove,
    isSuccess: isDeleteSuccess,
    isError: isDeleteError,
    error: errorDelete,
    reset: updateDelete,
  } = useDeleteAdmin();
  if (isDeleteError) {
    updateDelete();
    errorAlert(errorDelete);
  }
  if (isDeleteSuccess) {
    updateDelete();
    successAlert("Admin deleted successfully");
  }
  const data = useAdmin();

  const columns = [
    { title: "First Name", field: "firstname" },
    { title: "Last Name", field: "lastname" },
    { title: "Email", field: "email" },
    { title: "Phone", field: "phone" },
  ];

  const actions = (item) => [
    {
      name: "Edit",
      onClick: (res) => {
        setFormData(res);
        setEdit(true);
        setOpen(true);
      },
    },
    {
      name: "Delete",
      onClick: (res) => {
        if (window.confirm("Are you sure you want to delete?")) {
          remove(res);
        }
      },
    },
  ];
  const openHandler = () => {
    setOpen(!open);
    setEdit(false);
    setFormData("");
  };
  return (
    <Layout name="Admin">
      <div className="btnContainer">
        <button className="btn btnPartner" onClick={openHandler} type="button">
          Add Admin
        </button>
      </div>
      <Table data={data} columns={columns} actions={actions} />

      <Modal
        isVisible={open}
        title={edit ? "Edit Admin" : "Create Admin"}
        size="lg"
        content={
          <FormGroup
            onSubmit={edit ? updateHandler : submitHandler}
            validation={!edit && formData}
            errors={errors}
            setErrors={setErrors}
          >
            <Input
              name="firstname"
              label="First Name"
              value={formData["firstname"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.firstname}
              required={true}
              size="large"
            />
            <Input
              name="lastname"
              label="Last Name"
              value={formData["lastname"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.lastname}
              required={true}
              size="large"
            />
            <Input
              name="email"
              label="Email"
              value={formData["email"]}
              onChange={handleChange}
              type="email"
              reqType="email"
              validationHandler={validationHandler}
              error={errors.email}
              required={true}
              size="large"
            />
            <Input
              name="phone"
              label="Phone Number"
              value={formData["phone"]}
              onChange={handleChange}
              type="text"
              validationHandler={validationHandler}
              error={errors.phone}
              required={true}
              size="large"
            />
            {!edit && (
              <>
                <PasswordInput
                  name="password"
                  label="Password"
                  value={formData["password"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.password}
                  required={!edit}
                  size="large"
                />
                <PasswordInput
                  name="cpassword"
                  label="Confirm Password"
                  value={formData["cpassword"]}
                  onChange={handleChange}
                  type="text"
                  validationHandler={validationHandler}
                  error={errors.cpassword}
                  required={!edit}
                  size="large"
                />
              </>
            )}

            <Button
              title={edit ? "Update Admin" : "Add Admin"}
              loading={isLoading === 1}
              disabled={isLoading === 1}
              bgColor="btnPartner"
              size="small"
            />
            <Button
              title="Cancel"
              type="button"
              loading={isLoading === 1}
              disabled={isLoading === 1}
              bgColor="btnBlack"
              size="small"
              onClick={() => setOpen(!open)}
            />
          </FormGroup>
        }
        onClose={() => setOpen(false)}
        footer=""
      />
    </Layout>
  );
};

export default Admin;
